  margin-left: 0; margin-right: 0;
}
.issues-content__title {
  position: relative;
  margin: 0;
  text-align: center;
  font-size: 22px;
}
.js-inmobile[data-dpr='2'] .issues-content__title {
  font-size: 44px;
}
.js-inmobile[data-dpr='3'] .issues-content__title {
  font-size: 66px;
}
.issues-content__time {
  margin-top: 0; margin-bottom: 2em;
  font-size: 12px;
  text-align: center;
  color: #999;
}
.js-inmobile[data-dpr='2'] .issues-content__time {
  font-size: 24px;
}
.js-inmobile[data-dpr='3'] .issues-content__time {
  font-size: 36px;
}
/* base end */

/* tool start */
.position-vertical-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.transition-color-btn {
  padding: 0.3em 1em;
  border-radius: 3px;
  font-size: 12px;
  -webkit-transition-property: color, background-color;
  transition-property: color, background-color;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
.js-inmobile[data-dpr='2'] .transition-color-btn {
  font-size: 24px;
}
.js-inmobile[data-dpr='3'] .transition-color-btn {
  font-size: 36px;
}
.center-prompt-message {
  text-align: center;
  color: #999;
}
/* tool end */

/* app start */
.app-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  width: 700px;
  min-height: 100%;
  margin: 0 auto;
}
.js-inmobile .app-wrapper {
  width: 100%;
}
.app-header {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  position: relative;
  border-bottom: 1px solid #999;
}
.js-inmobile .app-wrapper h1 {
  margin: 0.3rem 0;
  text-align: center;
}
.app-header nav {
  position: relative;
  font-size: 14px;
}
.js-inmobile .app-header nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.js-inmobile[data-dpr="2"] .app-header nav {
  font-size: 28px;
}
.js-inmobile[data-dpr="3"] .app-header nav {
  font-size: 42px;
}
.app-header nav a {
  position: relative;
  display: table-cell;
  width: 6em;
  text-align: center;
}
.js-inmobile .app-header nav a {
  width: 33.33%;
}
.app-header nav a:hover,
.app-header nav .v-link-active {
  color: #0097da;
}
.app-header nav a:before {
  position: absolute;
  width: 100%;
  height: 0;
  left: 0;
  bottom: -1px;
  content: '';
  border-bottom: 3px solid #33ace1;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: -webkit-transform .3s ease;
  transition: -webkit-transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}
.app-header nav a:hover:before,
.app-header nav .v-link-active:before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.app-container {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  position: relative;
}
.js-inmobile .app-container {
  padding: 0 0.25rem;
}
.app-footer {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  border-top: 1px solid #999;
  text-align: center;
  font-size: 12px;
  color: #999;
}
.js-inmobile .app-footer {
  font-size: 1em;
}
.app-tools__top {
  display: none;
  position: fixed;
  bottom: 0.35rem;
  color: #999;
  cursor: pointer;
}
.js-inmobile .app-tools__top {
  right: 0.5rem;
}
.app-tools__top:hover {
  color: #f60;
}
.js-inmobile .app-tools__top:hover {
  color: #999;
}
.app-tools__top:active {
  color: #f26100;
}
.app-tools__top svg {
  fill: currentColor;
  width: 0.4rem; height: 0.4rem;
}
.js-inmobile .app-tools__top svg {
  width: 0.7rem; height: 0.7rem;
}
/* app end */

